* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header .logo {
  height: 40px;
}

.header .nav__list {
  margin: 0;
  padding: 0;
  display: flex;
}

.header .nav__list-li {
  align-items: center;
  margin-right: 20px;
  list-style: none;
  display: flex;
}

.header .nav__list-li:last-child {
  margin-right: 0;
}

.header .nav__list-li .nav__link {
  text-transform: uppercase;
  color: #060b35;
  box-sizing: border-box;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  transition-duration: .3s;
  position: relative;
}

.header .nav__list-li .nav__link:hover, .header .nav__list-li .is-active {
  color: #00acdc;
}

.header .nav__list-li .is-active:after {
  content: " ";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

p {
  margin: 0;
}

.container {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .container {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .container {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .container {
    grid-template-columns: repeat(4, 200px);
  }
}

.container .product {
  border: 1px solid #fff;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  transition-duration: .3s;
}

.container .product:hover {
  scale: 1.2;
}

.container .product__img {
  width: 100%;
  height: 134px;
  padding: 0 3px;
}

.container .product__title {
  color: #060b35;
  margin: 36px 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.container .product:hover .product__title {
  color: #34568b;
}

.container .product .code {
  color: #616070;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.container .product__popular {
  justify-content: space-between;
  align-items: center;
  margin: 16px 0 24px;
  display: flex;
}

.container .product__popular .stars {
  display: flex;
}

.container .product__popular .stars .star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.container .product__popular .stars .star:last-child {
  margin: 0;
}

.container .product__popular .stars--1 :first-child, .container .product__popular .stars--2 :nth-child(-n+2), .container .product__popular .stars--3 :nth-child(-n+3), .container .product__popular .stars--4 :nth-child(-n+4), .container .product__popular .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.container .product__popular .review {
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.container .product .price {
  justify-content: space-between;
  display: flex;
}

.container .product .price .title {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.container .product .price .cost {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.container .product .buy {
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.container .product .buy:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
  transition-duration: .3s;
}
